import * as React from 'react';
import { ContentPage } from '../../scripts/components';

      import { PageContent, Markdown } from '../../scripts/components';

      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/tutorials/02-API.md";
      const html = `
<h1 id="api-documentation">API Documentation</h1>
<h2 id="core-api">Core API</h2>
<p>AngleSharp has been created with a useful, yet standard complient API. If you just care about parsing a single document or stylesheet, then you can always just use the various parsers, such as the <code>HtmlParser</code> or the <code>CssParser</code>. In most cases (parsing and using webpages) we recommend the <code>BrowsingContext</code> located in the <code>AngleSharp</code> namespace. This namespace also contains some types with extension methods and a <code>Url</code> class that strictly follows the algorithms described in the WHATWG specification.</p>
<p>The <code>AngleSharp.Attributes</code> namespace also features the attributes being used to decorate the interfaces (and enumerations and delegates). Here we have:</p>
<ul>
<li><code>DomAccessorAttribute</code> for defining special accessors such as getters, setters or deleters</li>
<li><code>DomHistoricalAttribute</code> to indicate an obsolete status</li>
<li><code>DomDescriptionAttribute</code> to store a description string for a DOM part</li>
<li><code>DomNoInterfaceObjectAttribute</code> to declare a type being interfaced only, i.e. no object realization available</li>
<li><code>DomPutForwardsAttribute</code> to set the name of the method of the related object, where input should be forwarded</li>
<li><code>DomNameAttribute</code> to represent the original API name</li>
</ul>
<p>The API of the interfaces / DOM objects has been changed in such a way, that it is still the original DOM API (nothing is missing), but with naming and types being .NET conform and (hopefully) easier to work with.</p>
<h3 id="configuration">Configuration</h3>
<p>The <code>IConfiguration</code> is the main interface for extending AngleSharp. If we do not care, we do not have to supply AngleSharp with an instance of <code>IConfiguration</code> for parsing documents or URLs. In that scenario a default configuration will be considered.</p>
<p>This default configuration can be chosen by calling the method <code>SetDefault</code> on the <code>Configuration</code> class. We only need to pass in an instance of the custom configuration to use as the default configuration. The <code>Configuration</code> class is the default implementation of the <code>IConfiguration</code> interface. The default implementation is usually a good starting point for providing custom configurations.</p>
<p>The role of an <code>IConfiguration</code> object is to provide an enumeration of services to use or create for a browsing context, if any. While the services extend the DOM beyond a mostly static version.</p>
<p>The following example sets the <code>Culture</code> of the new <code>IConfiguration</code> object:</p>
<pre><code class="hljs language-cs"><span class="hljs-keyword">var</span> config = Configuration.Default.WithCulture(<span class="hljs-string">&quot;de-de&quot;</span>);
</code></pre>
<p>If no <code>Culture</code> is specified, the culture from the currently active thread will be taken.</p>
<p>Please note that the <code>Configuration</code> is immutable by default and all extension methods will either return the current instance (unmodified) or a new object (modified compared to the originally passed one).</p>
<p>Additionally, we might create our own class that is more convinient and flexible than the immutable implementation. We could either extend the default implementation, or implement the interface. Note, however, that the extension methods should actually make working with the <code>IConfiguration</code> pretty easy and straight forward.</p>
<p>Implementing the interface is also possible, but requires of course more work, since every property or methods needs to be re-implemented. However, since the properties of the default implementation are not <code>virtual</code>, it might be the only chance for providing the desired setup. In general there should be only few reasons to implement <code>IConfiguration</code> ourselves.</p>
<h2 id="extension-points">Extension Points</h2>
<p>AngleSharp is supposed to create an universal HTML5 parser that is accessible in the .NET world and written completely in managed code. However, some applications may want to go beyond the parser. The parser alone would require a lot of help from the outside to create the DOM.</p>
<p>In order to supply users of AngleSharp with a full DOM implementation, AngleSharp extends the HTML5 parser with a real DOM implementation. Nevertheless, this again brings some additional dependencies. What if, e.g., the <code>HtmlAudioElement</code> should play audio from its source? Of course one could just write a wrapper around the element, reading out the <code>Source</code> and supervising changes. But then this one wrapper would probably be incompatible (and / or behave differently) than the rest of the DOM.</p>
<p>This inconsistency is avoided by definining such external behavior in form of interfaces. Objects implementing such an interface might be registered to be used within AngleSharp.</p>
<p>In this page the various extension points will be listed and explained. Currently missing (but planned) interfaces will be sketched.</p>
<h3 id="istylingservice">IStylingService</h3>
<p>If a style (also external stylesheets in a <code>&lt;link&gt;</code> tag) or a script block is encountered, AngleSharp tries to find a matching engine. An implementation of <code>IStylingService</code> for instance looks at the provided mime-type and returns an associated <code>IStyleEngine</code> object or <code>null</code>. If the latter is encountered, another <code>IStylingService</code> object is tried, until a proper engine has been found, or all styling services have been asked. The same algorithm applies to <code>IScriptingService</code>. These services are only describing functionality of a factory, flyweight repository, or binding.</p>
<p>AngleSharp already includes a lightweight CSS parser (used for the CSS selectors). This was one of the design goals of AngleSharp. Additionally, it is required, since the (HTML) DOM is strongly coupled to CSS on some points. One example would be the <code>querySelector</code> and <code>querySelectorAll</code> methods. These methods require a CSS parser (or at least a CSS selector parser). In the end an object that is able to match certain elements is produced.</p>
<p>Nevertheless, an HTML browser may or may not know other styling possibilities than CSS. Currently CSS is used by default (and without specifying, e.g., <code>text/css</code> in the <code>type</code> attribute). However, one could register a style parser for a certain (or multiple) such types. Following this way, it is also possible to register a custom CSS parser, overriding the currently provided one.</p>
<p>An example of this is the official <code>AngleSharp.Css</code> library, which implements the full CSSOM.</p>
<h3 id="iscriptingservice">IScriptingService</h3>
<p>AngleSharp does not provide a script engine by default. Of course, any JavaScript engine would be a great addition, since JavaScript is the programming language of the web.</p>
<p>However, right now there are no intentions in providing an official / integrated solution. The <code>AngleSharp.Scripting</code> project that is contained our organization, is a sample and experimental project, to demonstrate how simple it is to write such an extension. Here we can start thinking about allowing C# as a scripting language. This would certainly be possible. Backed up with scriptcs or any other solution this would be a great addition, which could also be something different. In the long run it is great that AngleSharp supports multiple scripting engines.</p>
<p>Officially, we try to establish <code>AngleSharp.Js</code> as the solution. This is a separate project from the core, which requires high maintenance and lots of efforts. Any participation on this one would be highly appreciated.</p>
<h3 id="ispellcheckservice">ISpellCheckService</h3>
<p>Allows registration of individual spell-checkers. Each spell-checker is registered with its culture, making it sensitive to whatever culture the webpage or text is probably using.</p>
<p>The API allows ignoring certain words, running queries if a word is correctly spelled or getting suggestions for the correct spelling of a word. Right now the API has been implemented synchronously, but in the future that might switch to a better version, that is fully asynchronous.</p>
<h3 id="iresourceservice">IResourceService<IObjectInfo></h3>
<p>This extension is implemented in various interfaces, see:</p>
<ul>
<li><code>IAudioService</code>, for the <code>HtmlAudioElement</code></li>
<li><code>IVideoService</code>, for the <code>HtmlVideoElement</code></li>
<li><code>IImageService</code>, for the <code>HtmlImageElement</code></li>
<li><code>IObjectService</code>, for the <code>HtmlObjectElement</code></li>
</ul>
<p>The basic idea is to determine certain properties of the contained resource. The implementation of the specialization of <code>IResourceInfo</code> carries the resource dependent information, which would be the dimensions and more for an image. In case of an <code>HtmlAudioElement</code> the full media controller would be implemented as well, allowing playback of the media stream.</p>
<p>In case of the <code>HtmlObjectElement</code> this leads directly to plugins, such as Adobe Flash or others. Obviously the AngleSharp core is not responsible for these very specialized tasks.</p>
<h3 id="inavigator">INavigator</h3>
<p>Creates a <code>INavigator</code> instance for the given <code>IWindow</code> instance. The <code>INavigator</code> seems to be quite generic at first, however, it could be really specialized to the underlying <code>IWindow</code> instance, especially in terms of accessing media resources (e.g., webcam or microphone).</p>
<p>AngleSharp does not implement the interface in order to leave room for more adequate and specialized implementations. Also the previously described ability to enhance the user-experience with external peripherals seems appealing.</p>
<h3 id="ihistory">IHistory</h3>
<p>The <code>IHistory</code> interface is retrieved (usually in form of a creator) from the services. It describes the functionality to create a new DOM <code>IHistory</code> object, which will be associated with a browsing context.</p>
<h3 id="iwindow">IWindow</h3>
<p>There are multiple DOM elements that could be implemented in a more specialized and useful way. A crucial element is the <code>IWindow</code> implementation. It is not directly required, since all DOM interaction involves the <code>IDocument</code>, which is not dependent on the <code>IWindow</code>. However, especially in scripting environments, the <code>IWindow</code> instance fulfills a quite important role.</p>
<p>For more advanced scenarios, like rendering, a custom implementation of the <code>IWindow</code> interface seems important. Therefore the given service gives users the ability to register a custom <code>IWindow</code> creator. Note, that at the moment such a custom creator would be required to inherit at least from <code>EventTarget</code> (which implements <code>IEventTarget</code>). In the future this requirement will hopefully be omitted.</p>
<h3 id="iloader">ILoader</h3>
<p>Loading documents or resources in AngleSharp can be fully customized. The main interface is the <code>ILoader</code>, which is the base for two more specialized loaders. One is called <code>IDocumentLoader</code> and another to one is <code>IResourceLoader</code>. While the former is then used to load real documents in the context of a browsing context (hence there is max. one <code>IDocumentLoader</code> per <code>IBrowsingContext</code>), the latter is used for loading resources in the context of an <code>IDocument</code>. Obviously we require at most one <code>IResourceLoader</code> per <code>IDocument</code>.</p>
<p>There are two big advantages in this architecture:</p>
<ol>
<li>The responsibilities are clearly separated and every context (main or document) can track their own requests.</li>
<li>It is easy to turn off resource loading (even for particular elements) without affecting document loading / form submission at all.</li>
</ol>
<p>There is also a base implementation called <code>BaseLoader</code>.</p>
`;
    

export default () => (
  <ContentPage>
    
      <PageContent meta={{"title":"API","section":"General"}}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    
  </ContentPage>
);